module.exports = ".container {\n  margin-top: 40px;\n  text-align: center;\n  font-size: 1.8rem;\n}\n\n/* The switch - the box around the slider */\n\n.switch {\n  position: relative;\n  display: inline-block;\n  width: 60px;\n  height: 34px;\n}\n\n/* Hide default HTML checkbox */\n\n.switch input {\n  opacity: 0;\n  width: 0;\n  height: 0;\n}\n\n/* The slider */\n\n.slider {\n  position: absolute;\n  cursor: pointer;\n  top: 0;\n  left: 0;\n  right: 0;\n  bottom: 0;\n  background-color: #ccc;\n  transition: 0.4s;\n}\n\n.slider:before {\n  position: absolute;\n  content: '';\n  height: 26px;\n  width: 26px;\n  left: 4px;\n  bottom: 4px;\n  background-color: white;\n  transition: 0.4s;\n}\n\ninput:checked + .slider {\n  background-color: #2196f3;\n}\n\ninput:focus + .slider {\n  box-shadow: 0 0 1px #2196f3;\n}\n\ninput:checked + .slider:before {\n  -webkit-transform: translateX(26px);\n  transform: translateX(26px);\n}\n\n/* Rounded sliders */\n\n.slider.round {\n  border-radius: 34px;\n}\n\n.slider.round:before {\n  border-radius: 50%;\n}\n\n.number {\n  border-radius: 10px;\n  border: none;\n  background: #e5e5e5;\n  padding: 3px 10px;\n  text-align: center;\n  margin-bottom: 20px;\n}\n\n.button:disabled {\n  background: #e4e4e4;\n  color: #000;\n}\n\n.button {\n  border-radius: 20px;\n  border: none;\n  background: #0050a0;\n  color: #fff;\n  padding: 3px 10px;\n  text-align: center;\n  font-size: 20px;\n}\n\n/*# sourceMappingURL=data:application/json;base64, */"